const baseUrl = 'https://serverless-runtime-api.pagoda.com.cn/exc/community_ware_distribution'; //,
// const baseUrl = 'https://serverless-runtime-api.pagoda.com.cn/uat_exc/community_ware_distribution'; //,
var token=uni.getStorageSync('userMessage').token
if(token){
		token=uni.getStorageSync('userMessage').token
	}else{
		token=''
}	
// ()
const request = (opts) => {
	var token=uni.getStorageSync('userMessage').token
	if(token){
			token=uni.getStorageSync('userMessage').token
		}else{
			token=''
	}	
	let baseDefaultOpts = {
		url: baseUrl + opts.url,
		// 
		data: opts.data,
		// 
		method: opts.method,	
		// 
		header: opts.method == 'GET' ? {
			'X-Requested-With': 'XMLHttpRequest',
			"Accept": "application/json",
			"Content-Type": "application/json; charset=UTF-8",
			'authorization' :token,
		} : {
			'X-Requested-With': 'XMLHttpRequest',
			'Content-Type': 'application/json; charset=UTF-8',
			'authorization' :token,
		},
		// 
		dataType: 'json',
	};
			//  
			uni.showLoading({
				mask: false,
			});

	let promise = new Promise(function(resolve, reject) {
		uni.request(baseDefaultOpts).then(
			(res) => {
					setTimeout(function() {
					    uni.hideLoading();
					}, 1000);
					if(res[1].data.resultCode == '-1001' || res[1].data.resultCode == -1001){
							uni.showModal({
								title: '',
								content: '',
								showCancel:false,
								success: function(res) {
									if (res.confirm) {
										uni.removeStorageSync('userMessage')
										uni.switchTab({
											url:'../index/index'
										})
									} 
								}
							});
					}
					if(res[1].data.resultCode == '-1002' || res[1].data.resultCode == -1002){
							uni.showModal({
								title: '',
								content: ',',
								showCancel:false,
								success: function(res) {
									if (res.confirm) {
										uni.removeStorageSync('userMessage')
										uni.switchTab({
											url:'../index/index'
										})
									} 
								}
							});
					}
					resolve(res[1].data)				
			}
		).catch(
			(erro) => {
				setTimeout(function() {
				    uni.hideLoading();
				}, 1000);
				reject(erro)
			}
		)
	})
	return promise
};

export default request
